@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Festive&family=Keania+One&display=swap);
 html {
    box-sizing: border-box;
  }
  
  *, *::after, *::before {
    box-sizing: inherit;
  }
  
  body {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
    overflow-y: hidden;
  }
  
  a, button {
    font-family: 'Montserrat', sans-serif;
  }



